import React from "react";
import * as moment from "moment";
import { Button, Col, Row } from "reactstrap";
import _ from "lodash";
import cons from "../../constants";
import {
  getUserFullName,
  getDateFormat,
  getDurationTime,
  getDateTime,
  getTime,
} from "helpers/UtilsHelper";

import ConfirmDeleteModal from "../Modal/ConfirmDeleteModal.jsx";
import {
  deleteBooking,
  cancelBooking,
  getProviderData,
} from "../../redux/actions/bookingAction";
import { editAppointmentModal } from "../../redux/actions/providerDashboardActions";
import Appointment from "./Appointment.jsx";
import "./BookingSideBar.scss";
import { toast } from "react-toastify";

import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";

const now = moment();

function BookingSideBar(props) {
  const dispatch = useDispatch();
  const [isCancelBooking, setIsCancelBooking] = useState(false);
  const [isDeleteBooking, setIsDeleteBooking] = useState(false);

  const { venueData, dataSideBar, showSidebar, isAdmin, toggle } = props;

  const providerData = useSelector((state) => state.booking.providerData);
  const invoiceList = useSelector(
    (state) => state.invoice.appointmentInvoiceList
  );
  const loading = useSelector((state) => state.invoice.loading);

  const getSpaceName = (space) => {
    return _.get(space, "name", "Alternate space");
  };

  const getVenueName = (space) => {
    if (!space || !venueData || !venueData.length > 0) {
      return "Alternate venue";
    } else {
      const venue = venueData.find((v) => v.id === space.venue);
      return venue.name;
    }
  };

  const handleCancelBooking = () => {
    const countCancelationBalance = providerData.cancelationBalance;
    if (countCancelationBalance < 1) {
      toast.error("No more cancellations remaining.");
      toggleConfirmCancelBooking();
    } else {
      let { id } = dataSideBar;
      const data = { status: cons.BOOKING_STATUS.CANCELED };
      dispatch(cancelBooking(id, data));
      toggleConfirmCancelBooking();
    }
  };

  const handleDeleteBooking = () => {
    let { id } = dataSideBar;
    dispatch(deleteBooking(id));
    toggleConfirmDeleteBooking();
  };

  const toggleConfirmCancelBooking = () => {
    let providerId = dataSideBar.provider.id;
    dispatch(getProviderData(providerId));
    setIsCancelBooking(!isCancelBooking);
  };

  const toggleConfirmDeleteBooking = () => {
    setIsDeleteBooking(!isDeleteBooking);
  };

  const editAppointments = () => {
    dispatch(editAppointmentModal(props.dataSideBar));
  };

  const futureTime =
    dataSideBar &&
    getDateTime(`${dataSideBar.startDate} ${dataSideBar.startTime}`) >
      getDateTime(now._d)
      ? true
      : false;
  const activeStatus =
    dataSideBar && cons.BOOKING_STATUS.ACTIVE === dataSideBar.status
      ? true
      : false;
  const canCancelBooking = futureTime && activeStatus;
  const duration =
    dataSideBar &&
    getDurationTime(
      moment(dataSideBar.startTime, "HH:mm:ss"),
      moment(dataSideBar.endTime, "HH:mm:ss")
    );
  const unit = duration > 1 ? "hours" : "hour";
  return (
    <>
      <ConfirmDeleteModal
        showTitle={false}
        className="modal-dialog-centered"
        isOpen={isCancelBooking}
        toggle={toggleConfirmCancelBooking}
        handle={handleCancelBooking}
        content={`Are you sure you want to cancel this booking?`}
      />
      {isAdmin && (
        <ConfirmDeleteModal
          className="modal-dialog-centered"
          isOpen={isDeleteBooking}
          toggle={toggleConfirmDeleteBooking}
          handle={handleDeleteBooking}
          content={`Do you want to delete this booking?`}
        />
      )}
      {dataSideBar && showSidebar ? (
        <div id="sidebar" className="sidebar sidebar__wrapper wrapper">
          <div
            className={`sidebar ${
              showSidebar ? "sidebar-show" : "sidebar-hide"
            }`}
          >
            <span className={showSidebar ? "sidebar__icon" : "d-none"}>
              <Button onClick={toggle} className="sidebar__icon" color="link">
                <i className="fas fa-times fa-lg"></i>
              </Button>
            </span>
            <div className="sidebar__main">
              <div className="preview">
                <Row className="mt-4">
                  <Col className="time d-flex flex-column">
                    <div className="font-weight-bold">
                      {getDateFormat(dataSideBar.startDate, "MMMM DD, YYYY")}
                    </div>
                    <div className="font-weight-light color-light">
                      {" "}
                      {getTime(
                        `${dataSideBar.startDate} ${dataSideBar.startTime}`,
                        "hh:mma"
                      )}{" "}
                      to{" "}
                      {getTime(
                        `${dataSideBar.startDate} ${dataSideBar.endTime}`,
                        "hh:mma"
                      )}
                    </div>
                  </Col>

                  <Col
                    xs="3"
                    className="duration d-flex flex-column align-items-end"
                  >
                    <span className="value">{duration}</span>
                    <span className="label color-light">{unit}</span>
                  </Col>
                </Row>
                {!isAdmin && (
                  <Row className="mt-4">
                    <Col className="edit-button">
                      {
                        <div className="text-right">
                          <Button
                            color="link"
                            size="sm"
                            onClick={editAppointments}
                          >
                            <i className="fas fa-edit" /> Edit appointments
                          </Button>
                        </div>
                      }
                    </Col>
                  </Row>
                )}
                {dataSideBar.provider && (
                  <div className="client d-flex flex-column mt-4">
                    <label className="label-highlight">Provider</label>
                    <div className="my-2 ml-2">
                      <img
                        alt="..."
                        className="avatar avatar-sm rounded-circle mr-2"
                        src={_.get(
                          dataSideBar && dataSideBar.provider.avatar,
                          "provider.avatar",
                          require("assets/img/theme/default-avatar.png")
                        )}
                      />
                      <span>{getUserFullName(dataSideBar.provider)}</span>
                    </div>
                  </div>
                )}

                {!isAdmin &&
                dataSideBar.appointments &&
                dataSideBar.appointments.length > 0 ? (
                  <div className="client d-flex flex-column mt-4">
                    <label className="label-highlight">Member</label>
                    {dataSideBar.appointments.map((item) => {
                      const isCreate = !loading
                        ? !invoiceList.includes(item.id)
                        : false;
                      return (
                        <Appointment
                          isCreate={isCreate}
                          item={item}
                          key={item.id}
                          dataSideBar={dataSideBar}
                          isAdmin={isAdmin}
                          isEdit={canCancelBooking}
                        />
                      );
                    })}
                  </div>
                ) : (
                  ""
                )}

                {dataSideBar.space && dataSideBar.space.images.length > 0 ? (
                  <div className="space d-flex flex-column mt-4">
                    <label className="label-highlight">Space</label>
                    {dataSideBar.space &&
                      dataSideBar.space.images.length > 0 && (
                        <img alt="..." src={dataSideBar.space.images[0].file} />
                      )}
                    <span className="mt-1">
                      {getSpaceName(dataSideBar.space)}
                    </span>
                    <span className="color-light">
                      {getVenueName(dataSideBar.space)}
                    </span>
                  </div>
                ) : (
                  <div className="space d-flex flex-column mt-4">
                    <span className="mt-1">
                      {getSpaceName(dataSideBar.space)}
                    </span>
                    <span className="color-light">
                      {getVenueName(dataSideBar.space)}
                    </span>
                  </div>
                )}
                <div className="space d-flex flex-column mt-4">
                  <label className="label-highlight mb-0">
                    Booking Created
                  </label>
                  <span className="color-light">
                    {getDateTime(
                      dataSideBar.createdAt,
                      "MMMM DD, YYYY, hh:mma"
                    )}
                  </span>
                </div>
                <div className="space d-flex flex-column mt-4">
                  <label className="label-highlight mb-0">Price</label>
                  <span className="color-light">${dataSideBar.spacePrice}</span>
                </div>
                {/* <div className="space d-flex flex-column mt-4">
                  <label className="label-highlight mb-0">Rate</label>
                  <span className="color-light">----</span>
                </div> */}
              </div>
              <div className="sidebar__footer">
                <div className="d-flex flex-column">
                  {canCancelBooking && (
                    <Button
                      color="success"
                      onClick={toggleConfirmCancelBooking}
                    >
                      Cancel Booking
                    </Button>
                  )}
                  {isAdmin && (
                    <Button
                      color="danger"
                      className=""
                      onClick={toggleConfirmDeleteBooking}
                    >
                      Delete Booking
                    </Button>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      ) : (
        ""
      )}
    </>
  );
}

export default BookingSideBar;
