import React, { Component } from "react";
import { connect } from "react-redux";
import { ExportToCsv } from "export-to-csv";
import {
  Button,
  Card,
  Table,
  Row,
  Col,
  CardHeader,
  CardBody,
  Input,
  Label,
  ButtonGroup,
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
} from "reactstrap";
import ReactDatetime from "react-datetime";
import PaginationBar from "../../../components/PaginationBar";
import Loading from "../../../components/Loading";
import cons from "../../../constants";
import { getDate, getBookingsExportData } from "../../../helpers/UtilsHelper";
import moment from "moment";
import {
  getListExport,
  getList,
  getVenue,
  getSpace,
  setSpace,
  toggleSideBar,
} from "../../../redux/actions/bookingAction";
import Booking from "./Booking";
import BookingSideBar from "../../../components/BookingSidebar/BookingSideBar";
import "./ListBooking.scss";
import Form from "reactstrap/lib/Form";

const today = moment().startOf("date");

class ListBooking extends Component {
  constructor(props) {
    super(props);
    this.state = {
      status: "",
      space: "",
      venue: "",
      isLoading: true,
      isOpenSideBar: false,
      dataSideBar: null,
      start_date: today.clone(),
      end_date: today.clone().add(1, "M"),
      radio: 0,
      dropdownVenueOpen: false,
      dropdownSpaceOpen: false,
      dropdownVenueName: "Location",
      dropdownSpaceName: "Space",
      dropdownStatusOpen: false,
      dropdownStatusName: "All",
    };
    this.filters = {
      limit: 10,
      offset: 0,
      selected: 0,
      ordering: "start_date",
      desc: true,
      status: "",
      space: "",
      start_date: getDate(today.clone()),
      end_date: getDate(today.clone().add(1, "M")),
      venue: "",
    };
  }

  componentDidMount() {
    this.props.getList(this.filters);
    this.props.getVenue();
  }

  handleChangeFilter = async (e, itemName) => {
    const { value, name } = e.target;

    await this.setState({ [name]: value });
    if (name === "venue") {
      if (value === "") {
        await this.setState({ space: "", dropdownSpaceName: "Space" });
        await this.props.setSpace();
      } else {
        await this.props.getSpace(value);
      }
      await this.setState({ dropdownVenueName: itemName });
      this.filter();
    } else {
      await this.setState({ dropdownSpaceName: itemName });
      this.filter();
    }
  };

  handleChangeStatus = async (e, itemName, itemId) => {
    await this.setState({ dropdownStatusName: itemName, status: itemId });

    this.filter();
  };

  filterStatus = (status) => {
    this.setState({ status: status }, () => this.filter());
  };

  filter = () => {
    const { start_date, end_date, space, venue, status } = this.state;
    this.filters = {
      ...this.filters,
      space: space,
      venue: venue,
      status: status,
      start_date: start_date ? getDate(start_date._d) : "",
      end_date: end_date ? getDate(end_date._d) : "",
    };
    this.resetFilterData();
    this.props.getList(this.filters);
  };

  sort = (e) => {
    let name = e.target.attributes.name.value;
    if (name && name === this.filters.ordering) {
      this.filters.desc = !this.filters.desc;
    } else {
      this.filters.ordering = name;
      this.filters.desc = true;
    }
    this.resetFilterData();
    this.props.getList(this.filters);
  };

  selectPageSize = (e) => {
    const { value } = e.target;
    if (value) {
      this.resetFilterData();
      this.setState({ isOpenSideBar: false, tickCheckAll: false });
      this.filters.limit = value;
      this.props.getList(this.filters);
    }
  };

  selectPage = ({ selected }) => {
    this.filters.selected = selected || 0;
    this.filters.offset = Math.ceil(selected * this.filters.limit);
    this.setState({ isOpenSideBar: false, tickCheckAll: false });
    this.props.getList(this.filters);
  };

  handleReactDatetimeChange = async (who, date) => {
    if (
      this.state.start_date &&
      who === "end_date" &&
      new Date(this.state.start_date._d + "") > new Date(date._d + "")
    ) {
      await this.setState({
        start_date: date,
        end_date: date,
      });
      this.filter();
    } else if (
      this.state.end_date &&
      who === "start_date" &&
      new Date(this.state.end_date._d + "") < new Date(date._d + "")
    ) {
      await this.setState({
        start_date: date,
        end_date: date,
      });
      this.filter();
    } else {
      await this.setState({
        [who]: date,
      });
      this.filter();
    }
  };

  getClassNameReactDatetimeDays = (date) => {
    if (this.state.start_date && this.state.end_date) {
    }
    if (
      this.state.start_date &&
      this.state.end_date &&
      this.state.start_date._d + "" !== this.state.end_date._d + ""
    ) {
      if (
        new Date(this.state.end_date._d + "") > new Date(date._d + "") &&
        new Date(this.state.start_date._d + "") < new Date(date._d + "")
      ) {
        return " middle-date";
      }
      if (this.state.end_date._d + "" === date._d + "") {
        return " end-date";
      }
      if (this.state.start_date._d + "" === date._d + "") {
        return " start-date";
      }
    }
    return "";
  };

  resetFilterData = () => {
    this.filters.offset = 0;
    this.filters.selected = 0;
  };

  clearFilterData = () => {
    this.filters = {
      limit: 10,
      offset: 0,
      selected: 0,
      ordering: "startDate",
      desc: false,
      status: "",
      space: "",
      venue: "",
      start_date: "",
      end_date: "",
    };
    this.setState({
      start_date: "",
      end_date: "",
      status: "",
      space: "",
      venue: "",
      dropdownVenueName: "Location",
      dropdownSpaceName: "Space",
    });
    this.props.getList(this.filters);
  };

  toggleSideBar = (id, state) => {
    const bookingArr = this.props.bookingData.result;
    let dataSideBar = bookingArr.find((item) => item.id === id);
    const isOpenSideBar = state || !this.state.isOpenSideBar;
    this.setState({ dataSideBar });
    this.props.toggleSideBar(isOpenSideBar);
  };

  toggleSelectVenue = () => {
    this.setState({ dropdownVenueOpen: !this.state.dropdownVenueOpen });
  };
  toggleSelectStatus = () => {
    this.setState({ dropdownStatusOpen: !this.state.dropdownStatusOpen });
  };

  toggleSelectSpace = () => {
    this.setState({ dropdownSpaceOpen: !this.state.dropdownSpaceOpen });
  };

  callbackExportCSV = (dataResult) => {
    if (dataResult && dataResult.result.length > 0) {
      const data = getBookingsExportData(
        dataResult.result,
        this.props.venueData
      );
      const filename = String(moment().valueOf());
      const options = {
        filename,
        fieldSeparator: ",",
        quoteStrings: '"',
        decimalSeparator: ".",
        showLabels: true,
        showTitle: false,
        useTextFile: false,
        useBom: true,
        useKeysAsHeaders: true,
      };
      const csvExporter = new ExportToCsv(options);
      csvExporter.generateCsv(data);
    }
  };

  exportCSV = () => {
    // Call api get list booking by currently filter
    const filtersExport = this.filters;
    filtersExport.limit = 10000; // limit data

    this.props.getListExport(filtersExport, this.callbackExportCSV);
  };

  render() {
    const { bookingData, loading, venueData, spaceData, isOpenSideBar } =
      this.props;

    const bookingArr = bookingData.result;
    const { limit, selected } = this.filters;
    const {
      venue,
      status,
      dropdownVenueOpen,
      dropdownSpaceOpen,
      dropdownVenueName,
      dropdownSpaceName,
      dropdownStatusOpen,
      dropdownStatusName,
    } = this.state;
    const totalPage = Math.ceil(bookingData.count / limit) || 0;
    const paginationInfo = {
      from: selected * limit + 1,
      to:
        (selected + 1) * limit < bookingData.count
          ? (selected + 1) * limit
          : bookingData.count,
      total: bookingData.count,
      getShowOne: function () {
        return this.from === this.to;
      },
    };
    const paginationSetting = {
      totalPage,
      selected: this.filters.selected,
    };
    const sortClass = {
      title: this.filters.ordering,
      value: this.filters.desc,
      get: function (name) {
        if (this.title === name) {
          return `sort ${this.value ? "sort-desc" : "sort-asc"}`;
        }
        return "sort";
      },
    };

    return (
      <>
        <Col className="list-booking">
          <Card className="mt-3 custom-card">
            <BookingSideBar
              showSidebar={isOpenSideBar}
              toggle={this.toggleSideBar}
              dataSideBar={this.state.dataSideBar}
              isAdmin={true}
              venueData={venueData}
            />
            {loading && <Loading />}
            <CardHeader
              className="bg-transparent border-0"
              style={{ padding: "0" }}
            >
              <Row className="d-flex justify-content-between">
                <Col
                  className="d-flex justify-content-start justify-content-lg-start order-1 d-sm-flex flex-wrap flex-md-nowrap align-items-center mb-4 mb-lg-0 mr-auto ml-auto mr-md-0 ml-md-0"
                  xl={4}
                  lg={2}
                  xs={12}
                >
                  <ReactDatetime
                    className="header__custom-date-input"
                    inputProps={{
                      placeholder: "-- -- --",
                    }}
                    closeOnSelect={true}
                    value={this.state.start_date}
                    timeFormat={false}
                    dateFormat={"MM/DD/YYYY"}
                    onChange={(e) =>
                      this.handleReactDatetimeChange("start_date", e)
                    }
                    renderDay={(props, currentDate, selectedDate) => {
                      let classes = props.className;
                      classes +=
                        this.getClassNameReactDatetimeDays(currentDate);
                      return (
                        <td {...props} className={classes}>
                          {currentDate.date()}
                        </td>
                      );
                    }}
                  ></ReactDatetime>
                  <span className="d-md-block d-none">-</span>
                  <ReactDatetime
                    className="header__custom-date-input"
                    inputProps={{
                      placeholder: "-- -- --",
                    }}
                    closeOnSelect={true}
                    value={this.state.end_date}
                    timeFormat={false}
                    dateFormat={"MM/DD/YYYY"}
                    onChange={(e) =>
                      this.handleReactDatetimeChange("end_date", e)
                    }
                    renderDay={(props, currentDate, selectedDate) => {
                      let classes = props.className;
                      classes +=
                        this.getClassNameReactDatetimeDays(currentDate);
                      return (
                        <td {...props} className={classes}>
                          {currentDate.date()}
                        </td>
                      );
                    }}
                  ></ReactDatetime>
                  <i className="fas fa-calendar-alt d-md-block d-none"></i>
                </Col>

                <Col
                  className="d-flex justify-content-end justify-content-lg-end order-1 d-sm-flex flex-wrap flex-md-nowrap align-items-center mb-4 mb-lg-0 mr-auto ml-auto mr-md-0 ml-md-0"
                  xl={7}
                  lg={8}
                  xs={12}
                >
                  <ButtonGroup
                    className="btn-group-toggle header__btn-filter order-md-1 order-3"
                    data-toggle="buttons"
                  >
                    <Button
                      className="mb-2 mb-md-0 header__btn-filter border-0"
                      color="default"
                      outline
                      onClick={this.exportCSV}
                    >
                      <span>Export CSV</span>
                    </Button>
                  </ButtonGroup>

                  <Dropdown
                    isOpen={dropdownStatusOpen}
                    toggle={this.toggleSelectStatus}
                    className="mb-2 mb-md-0 header__dropdown order-md-2 order-1"
                  >
                    <DropdownToggle caret>
                      {/* <i className="fas fa-map-marker-alt mr-2" /> */}
                      {dropdownStatusName}
                    </DropdownToggle>
                    <DropdownMenu>
                      {cons.BOOKING_STATUS_INFO &&
                        cons.BOOKING_STATUS_INFO.map((item, i) => {
                          return (
                            <DropdownItem
                              key={i}
                              name="venue"
                              onClick={(e) =>
                                this.handleChangeStatus(
                                  e,
                                  `${item.status}`,
                                  item.id
                                )
                              }
                              value={item.id}
                            >
                              {item.status}
                            </DropdownItem>
                          );
                        })}
                    </DropdownMenu>
                  </Dropdown>

                  <Dropdown
                    isOpen={dropdownVenueOpen}
                    toggle={this.toggleSelectVenue}
                    className="mb-2 mb-md-0 header__dropdown order-md-2 order-1"
                  >
                    <DropdownToggle caret>
                      <i className="fas fa-map-marker-alt mr-2" />
                      {dropdownVenueName}
                    </DropdownToggle>
                    <DropdownMenu>
                      <DropdownItem
                        value=""
                        name="venue"
                        onClick={(e) => this.handleChangeFilter(e, "All")}
                      >
                        All
                      </DropdownItem>
                      {venueData &&
                        venueData.map((item, i) => {
                          return (
                            <DropdownItem
                              key={i}
                              name="venue"
                              onClick={(e) =>
                                this.handleChangeFilter(e, `${item.name}`)
                              }
                              value={item.id}
                            >
                              {item.name}
                            </DropdownItem>
                          );
                        })}
                    </DropdownMenu>
                  </Dropdown>

                  <Dropdown
                    isOpen={dropdownSpaceOpen}
                    toggle={this.toggleSelectSpace}
                    className="header__dropdown mb-2 mb-md-0 order-md-3 order-2"
                  >
                    <DropdownToggle caret>
                      <i className="ni ni-building mr-2"></i>
                      {dropdownSpaceName}
                    </DropdownToggle>
                    <DropdownMenu>
                      <DropdownItem
                        value=""
                        name="space"
                        onClick={(e) => this.handleChangeFilter(e, "All")}
                      >
                        All
                      </DropdownItem>
                      {venue &&
                        spaceData &&
                        spaceData.map((item, i) => {
                          return (
                            <DropdownItem
                              key={i}
                              name="space"
                              onClick={(e) =>
                                this.handleChangeFilter(e, `${item.name}`)
                              }
                              value={item.id}
                            >
                              {item.name}
                            </DropdownItem>
                          );
                        })}
                    </DropdownMenu>
                  </Dropdown>
                  <ButtonGroup
                    className="btn-group-toggle order-4"
                    data-toggle="buttons"
                  >
                    {/* <Button
                      className="mb-2 mb-md-0 header__btn-filter"
                      color="default"
                      outline
                      onClick={this.filter}
                    >
                      <span className="header__btn-filter__content">
                        Filter
                      </span>
                      <i className="fas fa-filter fa-xs header__btn-filter__content"></i>
                    </Button> */}
                    <Button
                      className="mb-2 mb-md-0 header__btn-filter"
                      color="default"
                      outline
                      onClick={this.clearFilterData}
                    >
                      <span className="header__btn-filter__content">
                        Clear Filters
                      </span>
                      <i className="fas fa-eraser header__btn-filter__content"></i>
                    </Button>
                  </ButtonGroup>
                </Col>
              </Row>
              <Row>
                <Col xl={3} lg={3} xs={12}>
                  {/* search bar and drom down */}

                  <Form onSubmit={this.onSearch}>
                    <Input type="text" name="search" placeholder="Search..." />
                  </Form>
                </Col>
              </Row>
            </CardHeader>
          </Card>
          <Card>
            <CardBody>
              <Table className="align-items-center table-sort" responsive>
                <thead>
                  <tr>
                    {/* <th className="table__header__w-95" /> */}
                    <th
                      className={` ${sortClass.get("space__name")}`}
                      name="space__name"
                      onClick={this.sort}
                    >
                      {" "}
                      Location{" "}
                    </th>
                    <th
                      className={` ${sortClass.get("space__name")}`}
                      name="space__name"
                      onClick={this.sort}
                    >
                      {" "}
                      Space{" "}
                    </th>
                    <th
                      className={`text-center ${sortClass.get(
                        "provider__first_name"
                      )}`}
                      name="provider__first_name"
                      onClick={this.sort}
                    >
                      {" "}
                      Provider{" "}
                    </th>
                    <th
                      className={`text-center ${sortClass.get(
                        "start_date,start_time"
                      )}`}
                      name="start_date,start_time"
                      onClick={this.sort}
                    >
                      {" "}
                      Date{" "}
                    </th>
                    <th
                      className={`text-center ${sortClass.get("duration")}`}
                      name="duration"
                      onClick={this.sort}
                    >
                      {" "}
                      Duration{" "}
                    </th>
                    <th
                      className={`text-center ${sortClass.get("status")}`}
                      name="status"
                      onClick={this.sort}
                    >
                      {" "}
                      Status{" "}
                    </th>
                  </tr>
                </thead>
                <tbody className="client">
                  {bookingArr &&
                    bookingArr.map((item, i) => {
                      return (
                        <Booking
                          key={i}
                          selectedItemId={
                            this.state.dataSideBar && this.state.dataSideBar.id
                          }
                          check={this.handleCheckbox}
                          openSideBar={this.toggleSideBar}
                          item={item}
                          setStatus={this.getProviderUpdateStatus}
                          venueData={venueData}
                        />
                      );
                    })}
                </tbody>
              </Table>
            </CardBody>
            {bookingArr && bookingArr.length > 0 && (
              <Row className="d-flex justify-content-md-between justify-content-sm-start mb-3 mx-0">
                <Col className="d-flex justify-content-center">
                  {totalPage > 1 && (
                    <PaginationBar
                      setting={paginationSetting}
                      handleClick={this.selectPage}
                    />
                  )}
                </Col>
                <Col
                  className="d-flex align-items-baseline mb-md-0 mb-2"
                  xs="auto"
                >
                  <Input
                    onChange={this.selectPageSize}
                    value={limit}
                    bsSize="md"
                    type="select"
                    name="selectPageSize"
                    id="selectPageSize"
                  >
                    <option value="5">5</option>
                    <option value="10">10</option>
                    <option value="20">20</option>
                    <option value="50">50</option>
                  </Input>
                  <Label className="ml-1 text-nowrap">
                    {/* <small>
                      {`${paginationInfo.from} ${
                        paginationInfo.getShowOne()
                          ? ""
                          : `- ${paginationInfo.to}`
                      } of ${paginationInfo.total}`}
                    </small> */}

                    <small>
                      Page {`${selected + 1}`} of {`${totalPage}`}
                    </small>
                  </Label>
                </Col>
              </Row>
            )}
          </Card>
        </Col>
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    bookingData: state.booking.bookingData,
    venueData: state.booking.venueData,
    spaceData: state.booking.spaceData,
    loading: state.booking.loadBookingData,
    isOpenSideBar: state.booking.isOpenSideBar,
  };
};

const mapActionToProps = {
  getListExport,
  getList,
  getVenue,
  getSpace,
  setSpace,
  toggleSideBar,
};

export default connect(mapStateToProps, mapActionToProps)(ListBooking);
