import React from 'react'
import _ from "lodash"
import { useDispatch } from 'react-redux';
import { getUserFullName, getTime } from 'helpers/UtilsHelper'
import { createAppointmentInvoice } from "../../redux/actions/invoiceAction"
import defaultAvatar from 'assets/img/theme/default-avatar.png'

function Appointment(props) {
    const dispatch = useDispatch()
    const { dataSideBar, item, isCreate } = props

    const createInvoice = () => {
        dispatch(createAppointmentInvoice(item.id))
    }

    return (
        <div className="appointment">
            <div className="my-2 ml-2">
                <img
                    alt="..."
                    className="avatar avatar-sm rounded-circle mr-2"
                    src={_.get(item, 'client.avatar', defaultAvatar) || defaultAvatar}
                />
                <span>{getUserFullName(item.client)}</span>
                <div className="ml-5 color-light mb-1"> {getTime(`${dataSideBar.startDate} ${item.startTime}`)} to {getTime(`${dataSideBar.startDate} ${item.endTime}`)} </div>
                {
                    isCreate && <div className="text-success ml-5 cursor-pointer" onClick={createInvoice}><b>+</b> Invoice</div>
                }
            </div>
        </div>
    )
}

export default Appointment;